import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Bpmn from '@/components/Bpmn'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/bpmn/create',
      name: 'bpmn-create',
      component: Bpmn
    },
    {
      path: '/bpmn/view',
      name: 'bpmn-view',
      component: HelloWorld
    }
  ]
})



